.root {
	max-width: 768px;
	flex-basis: 100%;
	padding: 30px;
	border-radius: 4px;
	box-shadow: 0 0 12px 4px rgba(0,0,0,0.3);
	background-color: ghostwhite;
	color: peru;
}
.title {
	margin-bottom: 30px;
	-webkit-text-stroke: 1px #333;
	font-size: 48px;
}
.result {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	position: relative;
	margin-bottom: 70px;
}
.copy {
	width: 20px;
	height: 30px;
	border: 2px solid #999;
	border-radius: 4px;
	cursor: pointer;
	position: relative;
	z-index: 2;
	outline: none;
	background-color: transparent;
}
.copy::after {
	display: block;
	content: '';
	width: 100%;
	height: 100%;
	border: 2px solid #999;
	position: absolute;
	bottom: -8px;
	right: -9px;
	border-radius: 4px;
	background-color: white;
}
.copy:active::after, .copy:active{
	background-color: maroon;
}
.copied {
	display: block;
	width: 100%;
	font-weight: bold;
	color: gray;
	text-align: center;
	position: absolute;
	bottom: -30px;
	left: 0;
	right: 0;
	animation-name: moveToTop;
	animation-duration: 0.25s;
	animation-iteration-count: 1;
}
@keyframes moveToTop {
	from {
		transform: translateY(50%);
	}
	to {
		transform: translateY(0);
	}
}
.option {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 30px;
}
.option-name {
	margin-right: 12px;
	font-weight: 600;
	color: #333;
	font-size: 20px;
}
.option-label {
	cursor: pointer;
	margin-right: 12px;
	font-weight: 600;
	color: #333;
	font-size: 20px;
}